/**
 * WebPack Entry main
 */
/** LESS */
require('../less/uikit.less');
require('../less/main.less');

/**
 * Imports
 */
import Vue from 'vue';
import VueRouter from 'vue-router';
import Vuex from 'vuex';
import App from './App.vue';
import {HTTP_Proxy as axios} from './utils/http_proxy';
import {routes} from './utils/routes';

/** AXIOS as http service for Vue */
Vue.prototype.$http = axios;

Vue.use(Vuex);
Vue.use(VueRouter);

import header from './Partials/Header.vue';

Vue.component('wp-header', header);

const Vuew = window.Vuew || {};

const router = new VueRouter({
    mode: 'history',
    base: '',
    routes: routes()
});

//Define vuex store
const store = new Vuex.Store({
    //HOLDS DATA
    state: {
        query: [
            {
                id: 0,
                type: 'post_type',
                type_value: 'post',
                rest_base: 'posts',
            }
        ]
    },
    //CANNOT BE CALLED DIRECTLY
    mutations: {
        UPDATE_QUERY: ( state, payload ) => {
            state.query = payload;
        },
    },
    //MUTATION PROXIES
    actions: {
        boot: (store, payload) => {
            //Boot data provided by WP
            store.commit( "UPDATE_QUERY", Vuew.boot );
        },
        navigate: (store, payload) => {
            store.commit( "UPDATE_QUERY", payload )
        },
    },
    //GET THE ACTUAL DATA
    getters : {
        getQueriedObject: state => state.query
    }
});

/*router.beforeEach((to, from, next) => {
    store.dispatch('navigate', {to});
    next();
});*/

new Vue({
    router,
    store,
    render: h => h(App)
}).$mount('#vuew-app');


