<template>

    <transition name="slide-fade">

        <div>
            <h2 class="uk-h2">{{ page.theTitle }} </h2>

            <ul class="uk-h3">
                <li>object_type: {{ object_type }} </li>
                <li>object_value: {{ object_value }} </li>
                <li>object_id: {{ object_id }} </li>
            </ul>

            <div class="uk-block" v-html="page.theContent"></div>

        </div>

    </transition>


</template>

<script>
    export default {

        /*props:[
            'object_type',
            'object_value',
            'object_id',
            'rest_base'
        ],*/

        mounted: function() {
            this.getPage();
        },

        data() {
            const vm = this;
            return {
                loaded: false,
                page: {},

                object_type : vm.$route.params.object_type,
                object_value: vm.$route.params.object_value,
                object_id   : vm.$route.params.object_id,
                rest_base   : vm.$route.params.rest_base
            };
        },
        methods: {
            getPage: function() {

                const vm = this;
                vm.loaded = false;

                let base = 'wp/v2/' + vm.$route.params.rest_base + '/' + vm.$route.params.object_id;

                console.log('PostType');

                vm.$http.get( base )
                    .then( ( res ) => {

                        vm.page = {
                            theTitle: res.data.title.rendered,
                            theContent: res.data.content.rendered
                        };
                        vm.loaded = true;

                    } )
                    .catch( ( res ) => {});

            }
        },
        watch: {
            '$route'(to, from) {
                console.log(to.params.object_type, this.object_type)
                if( to.params.object_type === this.object_type ){
                    this.getPage();
                }else{
                    this.$parent.getComponent();
                }
            }
        }

    };
</script>



